







































import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class ReportDrawBox extends Vue {
  @Prop({default: ''}) public readonly drawData!: string;
  @Prop({type: Number, default: 0}) public readonly selectedButton!: number;
  public setDrawText(dNum: number, gType: string) {
    if (gType === 'pk10' || gType === 'k3') {
      return '';
    }
    return dNum;
  }
  private setClass(buttonNumber: number, drawNum: any) {
    if (buttonNumber == 0) {
      return `num${drawNum}`;
    } else if (buttonNumber == 1) {
      return drawNum < 6 ? 'numsmall' : 'numbig';
    } else if (buttonNumber == 2) {
      return drawNum % 2 === 0 ? 'numdouble' : 'numsingle';
    } else {
      return 'numdragon';
    }
  }
  private mounted() {
    console.log(this.selectedButton);
  }
}
